<template>
  <div class="background">
    <div class="chat-container">
    <div class="chat-header">
      <h2 style="border-bottom: 1px;">在线客服</h2>
      <hr>
    </div>
    <div class="chat-messages" ref="messagesContainer">
      <div v-if="messages.length" class="message-container">
        <div v-for="(message, index) in messages" :key="index" :class="message.sender === '用户' ? 'user-message' : 'agent-message'">
          <div class="message-content">
            <div v-if="message.sender === '客服'" class="avatar">
              <i class=" el-icon-s-opportunity"></i>
            </div>
            <div class="text">{{ message.text }}</div>
            <div class="message-time">{{ formatTime(message.time) }}</div>
            <div v-if="message.sender === '用户'" class="avatar">
              <i class="el-icon-user"></i>
            </div>
          </div>
        </div>
      </div>
      <div v-else class="no-messages">
        <p>暂无消息</p>
      </div>
    </div>
    <div class="chat-input">
      <input type="text" v-model="newMessage" @keyup.enter="sendMessage" placeholder="输入消息">
      <button @click="sendMessage">发送</button>
    </div>
  </div>
    <div>
      <XtxFooter></XtxFooter>
    </div>
  </div>
</template>

<script>
import XtxFooter from '../components/XtxFooter.vue'
export default {
  data() {
    return {
      messages: [],
      newMessage: ''
    };
  },
  methods: {
    sendMessage() {
      if (this.newMessage) {
        const newMessage = {
          sender: '用户',
          text: this.newMessage,
          time: new Date()
        };
        this.messages.push(newMessage);
        this.newMessage = '';

        setTimeout(() => {
          const reply = {
            sender: '客服',
            text: '感谢您的咨询！',
            time: new Date()
          };
          this.messages.push(reply);
          this.$nextTick(() => {
            this.scrollToBottom();
          });
        }, 1000);
      }
    },
    formatTime(time) {
      const options = { hour: 'numeric', minute: 'numeric' };
      return new Date(time).toLocaleString('zh-CN', options);
    },
    scrollToBottom() {
      const messagesContainer = this.$refs.messagesContainer;
      messagesContainer.scrollTop = messagesContainer.scrollHeight;
    }
  },
  comments:{
    XtxFooter
  },
};
</script>



<style scoped>

.background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('@/assets/images/backpic.png');
  background-size: cover;
  background-position: center;
  opacity: 0.5; /* 背景图片透明度 */
  z-index: -1; /* 将背景图片置于内容之后 */
 
}

.chat-container {
  top: 20px;
  max-width: 700px;
  margin: 55px auto;
  border: 1px solid #000000;
  border-radius: 5px;
  padding: 20px;
  color: #000;
}

.chat-header {
  text-align: center;
  margin-bottom: 20px;
}

.chat-messages {
  min-height: 455px;
  max-height: 455px;
  overflow-y: auto;
}

.message-container {
  margin-bottom: 10px;
}

.user-message {
  display: flex;
  flex-direction: row-reverse;
  align-items: flex-start;
}

.agent-message {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

.message-content {
  display: flex;
  align-items: center;
  max-width: 70%;
  margin-bottom: 5px;
  position: relative;
}

.avatar {
  /* width: 30px; */
  height: 50px;
  border-radius: 50%;
  overflow: hidden;
  /* margin-right: 10px; */
}

.avatar img {
  width: 100%;
  height: 100%;
}

.text {
  padding: 10px;
  border-radius: 15px;
  background-color: #f0f0f0;
  white-space: pre-wrap;
}

.message-time {
  position: absolute;
  right: -75px;
  top: 0;
  font-size: 12px;
  color: #000000;
  text-align: center;
}

.no-messages {
  text-align: center;
}

.chat-input {
  display: flex;
  align-items: center;
}

.chat-input input {
  flex: 1;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.chat-input button {
  padding: 8px 15px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-left: 10px;
}
</style>