import Vue from 'vue'
import App from './App.vue'
import store from './store';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import './styles/base.css' // css 样式重置
import './styles/common.css' // 公共全局样式
import './assets/iconfont/iconfont.css' // 字体图标的样式

//引入router路由
import VueRouter from 'vue-router';


//通用组件
import BaseGoodsItem from './components/BaseGoodsItem'
import BaseGoodsItem2 from './components/BaseGoodsItem2'
import BaseBrandItem from './components/BaseBrandItem'
import XtxFooter from './components/XtxFooter.vue'

//路由组件
import MainUI from './views/MainUI.vue'
import MainUIChild from './views/MainUIChild.vue'
import GoodCourses from './views/GoodCourses.vue'
import UserLogin from './views/UserLogin.vue'
import UserRegister from './views/UserRegister.vue'
import PersonalCenter from './views/PersonalCenter.vue'
import HelpCenter from './views/HelpCenter.vue'
import OnlineService from './views/OnlineService.vue'
import PhoneService from './views/PhoneService.vue'
import MagazineUI from './views/MagazineUI.vue'
import NewsUI from './views/NewsUI.vue'
import LisenUI from './views/LisenUI.vue'
import MoreUI from './views/MoreUI.vue'

// import { component } from 'vue/types/umd'


Vue.component('BaseGoodsItem', BaseGoodsItem)
Vue.component('BaseGoodsItem2', BaseGoodsItem2)
Vue.component('BaseBrandItem', BaseBrandItem)
Vue.component('XtxFooter',XtxFooter)

Vue.config.productionTip = false
Vue.use(VueRouter)
Vue.use(ElementUI);

const router = new VueRouter({
  routes: [
    {path:'/',component:MainUI,children:[
      {path:'/',component:MainUIChild},
      {path:'/goodcourse',component:GoodCourses},
      {path:'/magazine',component:MagazineUI},
      {path:'/newsui',component:NewsUI},
      {path:'/listen',component:LisenUI},
      {path:'/more',component:MoreUI}

  ]},
    {path:'/login/:username',component:UserLogin},
    {path:'/user register/:username',component:UserRegister},
    {path:'/personal center',component:PersonalCenter},
    {path:'/help center',component:HelpCenter},
    {path:'/online service',component:OnlineService},
    {path:'/phone service',component:PhoneService},
  ]
})
new Vue({
  render: h => h(App),
  router,
  store
}).$mount('#app')
