<template>
<div>
  <section id="backpic">
        <!-- 登录 -->
        <div :class="logclass">
          <!-- 通过vue修改样式添加active实现动态的效果 -->
            <div class="user singinBx">
                <div class="imgBx"><img src="@/assets/images/magazine2.png" alt=""></div>
                <div class="formBx">
                  <form @submit.prevent="login">
                    <h2>登录</h2>
                    <input type="text" v-model="username" placeholder="用户名">
                    <input type="password" v-model="password" placeholder="密码">
                    <input type="submit" name="" value="登录">
                    <p style="color:#231fff" class="signup" @click="topggleForm()">没有账号？注册</p>
                    <!-- <p class="signup">没有账号？<a href="#" @click="topggleForm()">注册</a></p> -->
                  </form>
                </div>
            </div>

            <!-- 注册 -->

            <div class="user singupBx">
                <div class="formBx">
                    <form action="">
                        <h2>注册</h2>
                        <input type="text" name="" placeholder="用户名">
                        <input type="email" name="" placeholder="邮箱地址">
                        <input type="password" name="" placeholder="密码">
                        <input type="password" name="" placeholder="再次输入密码">
                        <input type="submit" name="" value="注册">
                        <p style="color:#231fff" class="signup" @click="topggleForm()">以有账号？登录</p>
                        <!-- <p class="signup">已有账号？<a href="#" @click="topggleForm()">登录</a></p> -->
                    </form>
                </div>
                <div class="imgBx"><img src="@/assets/images/magazine3.png" alt=""></div>
            </div>

        </div>
    </section>
    <XtxFooter></XtxFooter>
</div>
</template>

<script>
import XtxFooter from '../components/XtxFooter.vue'
import { mapActions } from "vuex";

export default {
  data(){
    return{
      username:'',
      password:'',
      logclass:'container'
    }
  },
  comments:{
    XtxFooter
  },
  methods: {

    ...mapActions(['login','uploadUserName']),
    login() {
        this.uploadUserName(this.username)
        console.log('login调用')
            // alert("登入成功") 之后跳转
            // this.$router.push({ name: 'UserProfile', params: { username: this.username } });
            this.$router.push({  path: '/'  }); // 这个地址要改
        },
    topggleForm() {
      if(this.logclass=='container')
      {
        this.logclass='container active'
      }
      else if(this.logclass=='container active')
      {
        this.logclass='container'
      }
      // return location.reload();
    }
  }
}
</script>

<style scoped>

*{
    padding: 0;
    margin:0;
    box-sizing: border-box;
    font-family: 'Poppins',sans-serif;
}


.signup {
    cursor: pointer;
}

/* 设置整个表单参数 (父盒子)*/

section {
    position: relative;
    min-height: 100vh;
    background: rgb(255, 255, 255);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
}

section .container {
    position: relative;
    width: 800px;
    height: 500px;
    background: #fff;
    box-shadow: 0 15px 50px rgba(0, 0, 0, 0.1);
    overflow: hidden;
}

section .container .user{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
}

/* 更改图片  （左侧）*/
section .container .imgBx{
    position: relative;
    width: 50%;
    height: 100%;
    /* background: #fff; */
    transition: .5s;
}

section .container .user .imgBx img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

/* 右侧表单盒子 */

section .container .user .formBx {
    position: relative;
    width: 50%;
    height: 100%;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 40px;
    transition: .5s;
}

/* h2 */

section .container .user .formBx form h2{
    font-size: 18px;
    font-weight: 600;
    text-transform: uppercase;/*大小*/
    letter-spacing: 2px;/* 间距*/
    text-align: center;
    width: 100%;
    margin-bottom: 10px;
    color: #555;
}

/* 表单文字属性 */

section .container .user .formBx form input{
    position: relative;
    width: 100%;
    padding: 10px;
    background: #f5f5f5;
    color: #333;
    border: none;
    outline:none;
    box-shadow:none;
    margin: 8px 0;
    font-size: 14px;
    letter-spacing:1px;
    font-weight: 300;
}

/* 为登录设置样式 */

section .container .user .formBx form input[type="submit"]{
    max-width: 100px;
    background: #677eff;
    color:#fff;
    cursor:pointer;
    font-size: 14px;
    font-weight: 500;
    letter-spacing: 1px;
    transition: .5s;
} 

/* 没有账号时 */

section .container .user .formBx form .signup{
    position: relative;
    margin-top: 20px;
    font-size: 12px;
    letter-spacing: 1px;
    color: #555;
    text-transform: uppercase;
    font-weight: 300;
}

section .container .user .formBx form .signup a{
    font-weight: 600;
    text-decoration: none;
    color: #677eff;
}
section .container .singupBx {
    pointer-events: none;
}

section .container.active .singupBx {
    pointer-events: initial;
}

section .container .singupBx .formBx {
    left: 100%;
}

section .container.active .singupBx .formBx {
    left: 0;
}

section .container .singupBx .imgBx {
    left: -100%;
}

section .container.active .singupBx .imgBx {
    left: 0;
}


section .container .singinBx .formBx {
    left: 0;
}

section .container.active .singinBx .formBx {
    left: 100%;
}

section .container .singinBx .imgBx {
    left: 0;
}

section .container.active .singinBx .imgBx {
    left: 100%;
}

@media (max-width: 991px) {
    section .container {
        max-width: 400px;
    }

    section .container .imgBx {
        display: none;
    }

    section .container .user .formBx {
        width: none;
    }
}

#backpic {
    background-image: url('@/assets/images/backpic2.JPG');
     /* 设置背景图片的大小，覆盖整个容器 */
    background-size: cover;
  
    /* 设置背景图片的位置，可以根据需要调整 */
    background-position: center;
  
    /* 如果需要固定背景图片，可以设置下面这个属性 */
    background-attachment: fixed;
  
    /* 如果背景图片无法显示时，设置备用背景颜色 */
    background-color: #f0f0f0;
  
    /* 设置容器的高度，根据实际需要调整 */
  
}

</style>