import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    username: '' // 初始状态为 null
  },
  mutations: {
    setUserName(state, name) {
      state.username = name;
    }
  },
  actions: {
    uploadUserName(context, name) {
      context.commit('setUserName', name); // 将数据提交给 mutation
    }
  },
  getters: {
    getUserName: state => state.username
  }
});